* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.card__review, .card__code {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__text {
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__price-cost {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.header {
  background: #fff;
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__link {
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  justify-content: center;
  align-items: center;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.nav .is-active {
  color: #00acdc;
  position: relative;
}

.nav .is-active:after {
  content: "";
  background-color: #00acdc;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.main {
  justify-content: center;
  display: flex;
}

.main__products {
  flex-wrap: wrap;
  flex-shrink: 0;
  align-items: center;
  width: 944px;
  padding-top: 50px;
  padding-bottom: 72px;
  display: flex;
}

.main__card {
  width: 200px;
  margin-bottom: 46px;
  margin-right: 48px;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  padding: 32px 16px 16px;
  display: flex;
}

.card:nth-child(4n) {
  margin-right: 0;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 3px 40px;
  display: block;
}

.card__text {
  color: #060b35;
  margin: 0 0 4px;
}

.card__code {
  color: #616070;
  margin: 0 0 16px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__review {
  color: #060b35;
  margin: 0;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-cost {
  color: #060b35;
  margin: 0;
}

.card__button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
